<template>
  <div id="app" >

      <nav class="mb-2 shadow">
      <div class="container">
        <ul class="nav__left">
          <li><router-link to="/"><i class="fa fa-home "></i> Home</router-link></li>
          <li><router-link to="/admin"><i class="fa fa-user"></i> Admin</router-link></li>
        </ul>
        <ul class="nav__right">
          <li><router-link to="/cart"><i class="fa fa-shopping-cart"></i> Cart ({{cartItemsCount.length}})</router-link></li>
          <li><router-link to="/signin"><i class="fa fa-lock"></i> Login/Register</router-link></li>
        
        </ul>
      </div>
    </nav>

    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {};
  },
  computed: {
    cartItemsCount() {
      return this.$store.getters.cart;
    }
  }
};
</script>

<style>
</style>



